import React from "react";
import Select from "react-select";

const SelectBtn = () => {
  const options = [
    { value: "up", label: "" },
    { value: "strawberry", label: "" },
    { value: "vanilla", label: "" },
    { value: "vanilla", label: "" },
    { value: "vanilla", label: "" },
  ];
  return (
    <div className="select">
      <Select options={options} placeholder="" />
    </div>
  );
};

export default SelectBtn;
