import React from "react";
import ItemList from "./ItemList";

const Item = ({ data }) => {
  return (
    <>
      <table>
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {data &&
            data.map((res) => {
              return <ItemList data={res} />;
            })}
        </tbody>
      </table>
    </>
  );
};

export default Item;
